/*LOGO*/

.navbar-logo {
    background: url(data:image/png;base64,) -80px -80px;
    font-family: fantasy;
    font-size: 30px;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    letter-spacing: 1px;
    z-index: 400;
    left: 42px;
    position: absolute;
}

.navbar-logo a{
    text-decoration: none;
}


/* Navbar width, background, etecetera*/
.navbar {
    background-image: -webkit-linear-gradient(top,
    rgb(16, 63, 78), rgb(2, 24, 31));
    background-image: linear-gradient(-900deg,
    rgb(16, 63, 78), rgb(2, 24, 31));
    height: 56px;
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    z-index: 1000; 
}

