Their components must  behave as it should do. Who knows what display itself?
Of course, the proper component does the required behavior**/

.navbar-menu * {
    right: 20px;
    background: none;
    position: absolute;
    display: inline
}

.navbar-menu a:hover {
    background-color: #0f0f0f;
    color: white;
}
.navbar-menu a {    
    background: rgb(177, 170, 170);
    color: rgb(0, 0, 0);
    padding: 10px 10px;
    text-decoration: none;
    font-family: 'Times New Roman', Times, serif;
}


.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.3);
    z-index: 100;
}

